import React, { useEffect, useState } from 'react'
import { useParams, useHistory } from 'react-router-dom'

import { Form, Row, Col, Input, Button, Typography, Space, Image, Divider, Card, Result, Spin } from 'antd'
import * as anticons from '@ant-design/icons'
import PasswordChecklist from 'react-password-checklist'
import { toast } from 'react-hot-toast'

import UsersService from '../service/UsersService'
const usersService = new UsersService()

const ResetPassword = () => {
    const { id } = useParams()
    const navigate = useHistory()
    const [validForm, setValidForm] = useState(false)
    const [vigente, setVigente] = useState(false)
    const [cargar, setCargar] = useState(true)
    const [cargando, setCargando] = useState(false)
    const [guardando, setGuardando] = useState(false)
    const [user, setUser] = useState({ id, password: '', passwordConfirm: '' })

    const onPasswordChange = (password) => setUser({ ...user, ...{ password } })
    const onPasswordConfirmChange = (passwordConfirm) => setUser({ ...user, ...{ passwordConfirm } })

    const go = () => {
        navigate.push('/inicio')
    }

    const getResetPassword = async () => {
        setCargando(true)
        try {
            const datos = { id }
            const { data } = await usersService.getResetPassword({ datos })
            if (data.length > 0) {
                setVigente(true)
            }
        } catch ({ response }) {
            const { data } = response
            toast.error(data.detalle);
        } finally {
            setCargar(false)
            setCargando(false)
        }
    }

    const resetPassword = async () => {
        setGuardando(true)
        try {
            const { data } = await usersService.resetPassword({ user })
            const { detalle, estado } = data
            if (estado === true) {
                toast.success(detalle)
                setTimeout(() => {
                    navigate.push('/login')
                }, 3000);
            } else {
                toast.error(detalle)
            }
        } catch ({ response }) {
            const { data } = response
            toast.error(data.detalle);
        } finally {
            setGuardando(false)
        }
    }

    const failedVerified = ({ values, errorFields, outOfDate }) => {
    }

    useEffect(() => {
        if (cargar && !cargando) {
            getResetPassword()
        }
    })

    return (
        <>
            <div
                style={{
                    color: "#333333",
                    padding: "20px 0",
                }}
            >
                <Image
                    alt="BYDZYNE"
                    src="/assets/images/logoBlack.png"
                    style={{ height: "80px", width: "80px" }}
                />
                <span
                    style={{
                        marginLeft: "20px",
                        fontSize: "1.5em",
                        letterSpacing: "3px",
                        fontWeight: "bold",
                    }}
                >
                    BYDZYNE
                </span>
            </div>
            <Row justify='center'>
                <Col xs={24} sm={16} md={12} lg={10} xl={8} xxl={6} style={{ textAlign: 'center' }}>
                    <Typography.Title level={3}>
                        Restablecer Contrasea
                    </Typography.Title>
                    <Divider />
                </Col>
            </Row>
            <Row justify='center'>

                <Col xs={24} sm={16} md={12} lg={10} xl={8} xxl={6}>
                    <Card
                        bordered
                        style={{
                            backgroundColor: 'rgba(255,255,255, 0.9)',
                            padding: '10px 10px'
                        }}
                    >
                        {
                            cargar === true || cargando === true ?
                                <div style={{
                                    textAlign: 'center'
                                }} >
                                    <Spin
                                        size='large'
                                        indicator={
                                            <anticons.LoadingOutlined
                                                style={{
                                                    fontSize: 32
                                                }} />
                                        }
                                    />
                                    <Typography.Paragraph>
                                        <br />Espere un momento...
                                    </Typography.Paragraph>
                                </div> :
                                vigente === true ?
                                    <Form
                                        layout={'vertical'}
                                        initialValues={user}
                                        onFinish={resetPassword}
                                        onFinishFailed={failedVerified}
                                        disabled={guardando}
                                    >
                                        <Row gutter={10}>
                                            <Col span={24}>

                                                <Form.Item
                                                    name="password"
                                                    label={
                                                        <Typography.Text>
                                                            Contrasea
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Ingrese la contrasea',
                                                        },
                                                    ]}>
                                                    <Input.Password onChange={(e) => onPasswordChange(e.target.value)} />
                                                </Form.Item>
                                                <Form.Item
                                                    name="passwordConfirm"
                                                    label={
                                                        <Typography.Text>
                                                            Confirmar Contrasea
                                                        </Typography.Text>
                                                    }
                                                    dependencies={['password']}
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Confirme la contrasea',
                                                        },
                                                        ({ getFieldValue }) => ({
                                                            validator(_, value) {
                                                                if (!value || getFieldValue('password') === value) {
                                                                    return Promise.resolve();
                                                                }
                                                                return Promise.reject(new Error('Las contraseas no coinciden'));
                                                            },
                                                        }),
                                                    ]}>
                                                    <Input.Password onChange={(e) => onPasswordConfirmChange(e.target.value)} />
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row gutter={10}>
                                            <Col span={24} style={{ paddingTop: '10px' }}>
                                                {
                                                    user.password &&
                                                    <PasswordChecklist
                                                        rules={["minLength", "specialChar", "number", "capital", "match"]}
                                                        minLength={8}
                                                        value={user.password}
                                                        valueAgain={user.passwordConfirm}
                                                        messages={{
                                                            minLength: "La contrasea tiene ms de 8 caracteres.",
                                                            specialChar: "La contrasea tiene caracteres especiales.",
                                                            number: "La contrasea tiene un nmero.",
                                                            capital: "La contrasea tiene una letra mayscula.",
                                                            match: "Las contraseas coinciden",
                                                        }}
                                                        onChange={(isValid) => {
                                                            setValidForm(isValid)
                                                        }}
                                                    />
                                                }
                                            </Col>
                                        </Row>
                                        <Divider />
                                        <Form.Item>
                                            <Row justify='center'>
                                                <Col>
                                                    <Space>
                                                        <Button type="primary" htmlType='submit' disabled={guardando || !validForm} loading={guardando}>
                                                            {guardando ? 'Restableciendo Contrasea...' : 'Restablecer Contrasea'}
                                                        </Button>
                                                    </Space>
                                                </Col>
                                            </Row>
                                        </Form.Item>
                                    </Form>
                                    :
                                    <Result
                                        title="La solicitud de restablecimiento se encuentra caducada."
                                        extra={
                                            <Button type="primary" key="console" onClick={() => go()}>
                                                Ir a Inicio
                                            </Button>
                                        }
                                    />
                        }
                    </Card>
                </Col>
            </Row>
        </>
    )
}

export default ResetPassword