import React, { useState } from 'react'
import { useHistory } from 'react-router-dom'

import { Form, Row, Col, Input, Button, Typography, Space, Image, Divider, Card, Spin, Result } from 'antd'
import * as anticons from '@ant-design/icons'
import { toast } from 'react-hot-toast'

import UsersService from '../service/UsersService'
const usersService = new UsersService()

const ForgotPassword = () => {

    const navigate = useHistory()
    const [enviado, setEnviado] = useState(false)
    const [enviando, setEnviando] = useState(false)

    const goLogin = () => navigate.push('/login')

    const sendResetPassword = async ({ correo }) => {
        setEnviando(true)
        setEnviado(false)
        const user = {
            correo,
            responsable: correo
        }
        try {
            const { data } = await usersService.sendResetPassword({ user })
            const { detalle, estado } = data
            if (estado === true) {
                setEnviado(true)
                toast.success(detalle)
            } else {
                toast.error(detalle)
            }
        } catch ({ response }) {
            const { data } = response
            toast.error(data.detalle);
        } finally {
            setEnviando(false)
        }
    }

    if (enviado === true && enviando === false) {
        return (
            <>
                <div
                    style={{
                        color: "#333333",
                        padding: "20px 0",
                    }}
                >
                    <Image
                        alt="BYDZYNE"
                        src="/assets/images/logoBlack.png"
                        style={{ height: "80px", width: "80px" }}
                    />
                    <span
                        style={{
                            marginLeft: "20px",
                            fontSize: "1.5em",
                            letterSpacing: "3px",
                            fontWeight: "bold",
                        }}
                    >
                        BYDZYNE
                    </span>
                </div>
                <Row justify='center'>
                    <Col xs={24} sm={16} md={12} lg={10} xl={8} xxl={6} style={{ textAlign: 'center' }}>
                        <Card
                            bordered
                            style={{
                                backgroundColor: 'rgba(255,255,255, 0.9)',
                                padding: '10px 10px'
                            }}
                        >
                            <Result
                                title="Solicitud Enviada"
                                subTitle={`La solicitud de restablecimiento de contrasea fue enviada al correo indicado.`}
                                extra={
                                    <Button type="primary" key="console" onClick={() => goLogin()}>
                                        Ingresar
                                    </Button>
                                }
                            />
                        </Card>
                    </Col>
                </Row>
            </>
        )
    }

    return (
        <>
            <div
                style={{
                    color: "#333333",
                    padding: "20px 0",
                }}
            >
                <Image
                    alt="BYDZYNE"
                    src="/assets/images/logoBlack.png"
                    style={{ height: "80px", width: "80px" }}
                />
                <span
                    style={{
                        marginLeft: "20px",
                        fontSize: "1.5em",
                        letterSpacing: "3px",
                        fontWeight: "bold",
                    }}
                >
                    BYDZYNE
                </span>
            </div>
            <Row justify='center'>
                <Col xs={24} sm={16} md={12} lg={10} xl={8} xxl={6} style={{ textAlign: 'center' }}>
                    <Typography.Title level={3}>
                        Restablecer Contrasea
                    </Typography.Title>
                    <Divider />
                </Col>
            </Row>
            <Row justify='center'>
                <Col xs={24} sm={16} md={12} lg={10} xl={8} xxl={6}>
                    <Card
                        bordered
                        style={{
                            backgroundColor: 'rgba(255,255,255, 0.9)',
                            padding: '10px 10px'
                        }}
                    >
                        {
                            enviando === true ?
                                <div style={{
                                    textAlign: 'center'
                                }} >
                                    <Spin
                                        size='large'
                                        indicator={
                                            <anticons.LoadingOutlined
                                                style={{
                                                    fontSize: 32
                                                }} />
                                        }
                                    />
                                    <Typography.Paragraph>
                                        <br />Espere un momento...
                                    </Typography.Paragraph>
                                </div> :
                                <Form
                                    layout={'vertical'}
                                    initialValues={{
                                        correo: ''
                                    }}
                                    onFinish={sendResetPassword}
                                    disabled={enviando}
                                >
                                    <Row gutter={10}>
                                        <Col span={24}>
                                            <Form.Item
                                                name="correo"
                                                label={
                                                    <Typography.Text>
                                                        Ingrese su correo electrnico
                                                    </Typography.Text>
                                                }
                                                rules={[
                                                    {
                                                        required: true,
                                                        message: 'Ingrese su correo electrnico',
                                                    },
                                                ]}>
                                                <Input />
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Divider />
                                    <Form.Item>
                                        <Row>
                                            <Col span={24} style={{ textAlign: 'center' }}>
                                                <Space direction='vertical'>
                                                    <Button type="primary" htmlType='submit' disabled={enviando} loading={enviando}>
                                                        {enviando ? 'Restableciendo Contrasea...' : 'Restablecer Contrasea'}
                                                    </Button>
                                                    <Button type='default' key="console" onClick={() => goLogin()}>
                                                        Ingresar
                                                    </Button>
                                                </Space>
                                            </Col>
                                        </Row>
                                    </Form.Item>
                                </Form>
                        }
                    </Card>
                </Col>
            </Row>
        </>
    )
}

export default ForgotPassword