import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Card, Col, Image, Row, Form, Button, Typography, Input, Select, Alert, Space, Result, Spin } from 'antd'
import * as anticons from '@ant-design/icons'

import toast from 'react-hot-toast'
import PasswordChecklist from 'react-password-checklist'

import AuthenticationService from '../service/AuthenticationService'
import SupervisorService from '../service/SupervisorService'
import PaisService from '../service/PaisService'
const authenticationService = new AuthenticationService()
const supervisorService = new SupervisorService()
const paisService = new PaisService()

const Register = () => {

    const navigate = useHistory()
    const [cargar, setCargar] = useState(true)
    const [cargando, setCargando] = useState(false)
    const [guardando, setGuardando] = useState(false)
    const [message, setMessage] = useState(null)
    const [presidentes, setPresidentes] = useState([])
    const [paises, setPaises] = useState([])
    const [password, setPassword] = useState("")
    const [passwordConfirm, setPasswordConfirm] = useState("")
    const [registrado, setRegistrado] = useState(false)

    const go = () => {
        navigate.push('/login')
    }

    const guardar = async (datos) => {
        const datosUsuario = { ...datos, ...{ activo: datos.activo === true ? '1' : '0', responsable: datos.identificacion } }
        setGuardando(true)
        setMessage(null)
        try {
            const { data } = await authenticationService.register(datosUsuario)
            const { detalle, estado } = data
            if (estado) {
                toast.success(detalle)
                setRegistrado(true)
            }
            else {
                toast.error(detalle)
            }
        } catch ({ response }) {
            const { data } = response
            setMessage(data.retorno.indexOf('IDX_CorreoUnico') > 0 ? `El correo ${datosUsuario.correo} ya se encuentra registrado` : data.retorno.indexOf('IDX_PaisIdentificacionUnico') > 0 ? `La identificacin ${datosUsuario.identificacion} ya se encuentra registrada` : data.detalle)
        } finally {
            setGuardando(false)
        }
    }

    const fetchData = async () => {
        setCargar(false)
        setCargando(true)
        const responsable = 'anonymous'
        const requests = [
            supervisorService.gets({ responsable })
            , paisService.gets({ responsable })
        ]
        try {
            const responses = await Promise.all(requests)
            const presidentes = responses[0].data
            setPresidentes(presidentes)
            const paises = responses[1].data
            setPaises(paises)
        } catch (responses) {
        } finally {
            setCargando(false)
        }
    }

    useEffect(() => {
        if (cargar) {
            fetchData()
        }
    })

    return (
        <div style={{ width: '100%', backgroundColor: '#000000' }}>
            <div style={{
                background: 'linear-gradient(to right, rgba(163,43,213,0.35), rgba(20,84,219,0.45))',
                width: '100%',
                height: '100%'
            }}>
                <div style={{
                    color: '#ffffff',
                    borderBottom: 'solid 1px #666666',
                    padding: '20px 30px',
                }}>
                    <Image alt='BYDZYNE' src='/assets/images/logo.png' style={{ height: '60px', width: '60px' }} />
                    <span style={{
                        marginLeft: '20px',
                        fontSize: '1.5em',
                        letterSpacing: '3px',
                        fontWeight: 'bold'
                    }}>
                        BYDZYNE
                    </span>
                </div>
                <div style={{ padding: '40px 20px' }}>
                    <Row justify='center'>
                        <Col xs={24} sm={22} md={20} lg={16} xl={14} xxl={10}>
                            {
                                cargar === true && cargando === true ?
                                    <div style={{
                                        textAlign: 'center'
                                    }} >
                                        <Spin
                                            size='large'
                                            indicator={
                                                <anticons.LoadingOutlined
                                                    style={{
                                                        fontSize: 32
                                                    }} />
                                            }
                                        />
                                        <Typography.Paragraph>
                                            <br />Espere un momento...
                                        </Typography.Paragraph>
                                    </div>
                                    :
                                    registrado === true ?
                                        <Card
                                            title={<Typography.Title level={4} style={{ color: '#333333', letterSpacing: '0.2em', paddingBottom: '10px 0', textAlign: 'center' }}>
                                                Registro
                                            </Typography.Title>}
                                            bordered
                                            style={{
                                                backgroundColor: 'rgba(255,255,255, 0.9)',
                                                padding: '10px 10px'
                                            }}
                                        >
                                            <Result
                                                title="Gracias por registrarse en Sistema Equipo Pro."
                                                subTitle="Su solicitud se encuentra en proceso de aprobacin."
                                                extra={
                                                    <Button type="primary" key="console" onClick={() => go()}>
                                                        Ir a Inicio
                                                    </Button>
                                                }
                                            />
                                        </Card>
                                        :
                                        <Card
                                            title={<Typography.Title level={4} style={{ color: '#333333', letterSpacing: '0.2em', paddingBottom: '10px 0', textAlign: 'center' }}>
                                                Registro
                                            </Typography.Title>}
                                            bordered
                                            style={{
                                                backgroundColor: 'rgba(255,255,255, 0.9)',
                                                padding: '10px 10px'
                                            }}
                                        >
                                            <Form
                                                layout={'vertical'}
                                                initialValues={{}}
                                                onFinish={guardar}
                                                disabled={guardando}
                                            >
                                                <Row gutter={10}>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                        <Form.Item
                                                            name="identificacion"
                                                            label={
                                                                <Typography.Text>
                                                                    Identificacin
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Ingrese la identificacin',
                                                                },
                                                            ]}
                                                        >
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                        <Form.Item
                                                            name="nombres"
                                                            label={
                                                                <Typography.Text>
                                                                    Nombres
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Ingrese los nombres',
                                                                },
                                                            ]}>
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                </Row>
                                                <Row gutter={10}>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                        <Form.Item
                                                            name="apellidoPaterno"
                                                            label={
                                                                <Typography.Text>
                                                                    Apellido Paterno
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Ingrese el apellido paterno',
                                                                },
                                                            ]}>
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                        <Form.Item
                                                            name="apellidoMaterno"
                                                            label={
                                                                <Typography.Text>
                                                                    Apellido Materno
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Ingrese el apellido materno',
                                                                },
                                                            ]}>
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                </Row>
                                                <Row gutter={10}>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                        <Form.Item
                                                            name="idPais"
                                                            label={
                                                                <Typography.Text>
                                                                    Pais
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Seleccione el pais',
                                                                },
                                                            ]}>
                                                            <Select
                                                                showSearch
                                                                options={paises}
                                                                fieldNames={{
                                                                    label: 'nombre',
                                                                    value: 'id'
                                                                }}
                                                                optionFilterProp="nombre"
                                                            />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                        <Form.Item
                                                            name="telefono"
                                                            label={
                                                                <Typography.Text>
                                                                    Telfono
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Ingrese el telfono',
                                                                },
                                                            ]}>
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                </Row>
                                                <Row gutter={10}>
                                                    <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24}>
                                                        <Form.Item
                                                            name="idResponsable"
                                                            label={
                                                                <Typography.Text>
                                                                    Presidente
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Seleccione el presidente responsable',
                                                                },
                                                            ]}>
                                                            <Select
                                                                showSearch
                                                                options={presidentes}
                                                                fieldNames={{
                                                                    label: 'nombres',
                                                                    value: 'id'
                                                                }}
                                                                optionFilterProp="nombres"
                                                            />
                                                        </Form.Item>
                                                    </Col>
                                                </Row>
                                                <Row gutter={10}>
                                                    <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24}>
                                                        <Form.Item
                                                            name="correo"
                                                            label={
                                                                <Typography.Text>
                                                                    Correo
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Ingrese el correo',
                                                                },
                                                            ]}>
                                                            <Input />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                        <Form.Item
                                                            name="password"
                                                            label={
                                                                <Typography.Text>
                                                                    Contrasea
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Ingrese la contrasea',
                                                                },
                                                            ]}>
                                                            <Input.Password onChange={e => setPassword(e.target.value)} />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                        <Form.Item
                                                            name="passwordConfirm"
                                                            label={
                                                                <Typography.Text>
                                                                    Confirme la Contrasea
                                                                </Typography.Text>
                                                            }
                                                            rules={[
                                                                {
                                                                    required: true,
                                                                    message: 'Confirme la contrasea',
                                                                },
                                                            ]}>
                                                            <Input.Password onChange={e => setPasswordConfirm(e.target.value)} />
                                                        </Form.Item>
                                                    </Col>
                                                    <Col span={24}>
                                                        <PasswordChecklist
                                                            rules={["minLength", "specialChar", "number", "capital", "match"]}
                                                            minLength={8}
                                                            value={password}
                                                            valueAgain={passwordConfirm}
                                                            messages={{
                                                                minLength: "La contrasea tiene ms de 8 caracteres.",
                                                                specialChar: "La contrasea tiene caracteres especiales.",
                                                                number: "La contrasea tiene un nmero.",
                                                                capital: "La contrasea tiene una letra mayscula.",
                                                                match: 'Las contraseas no coinciden'
                                                            }}
                                                        />
                                                    </Col>
                                                </Row>
                                                <Form.Item>
                                                    <Row justify='center'>
                                                        <Col>
                                                            {
                                                                message && <Alert message={message} type='warning' />
                                                            }
                                                        </Col>
                                                    </Row>
                                                    <Row justify='end' style={{ paddingTop: '15px' }}>
                                                        <Col>
                                                            <Space>
                                                                <Button type='default' onClick={() => go()}>Regresar</Button>
                                                                <Button type="primary" htmlType='submit' disabled={guardando} loading={guardando}>
                                                                    {guardando ? 'Enviando...' : 'Enviar'}
                                                                </Button>
                                                            </Space>
                                                        </Col>
                                                    </Row>
                                                </Form.Item>
                                            </Form>
                                        </Card>
                            }
                        </Col>
                    </Row>
                </div>
            </div>
        </div>
    )
}

export default Register