import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Card, Col, Layout, Row, Typography, Image } from 'antd';
import { toast } from "react-hot-toast";
import Loader from './../components/Loader'

import { getUserName } from "../utilities";
import HallFamaService from './../service/HallFamaService'
import UsersService from "../service/UsersService";
const hallFamaService = new HallFamaService()
const usersService = new UsersService();

const HallFama = () => {

    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const { titulo, imagen, botones, items, detalle } = hallFamaService.get()

    const go = (url) => navigate.push(url)

    const open = (url) => window.open(url)

    const renderHallItem = ({ id, codigo, url, video }) => {
        return <Col key={id} xs={24} sm={24} md={12} lg={12} xl={12} xxl={12}>
            {
                video ?
                    video
                    :
                    url ?
                        <Image alt={codigo} className='bdRoundBorder bdPuntero' src={`/assets/images/hall/${codigo}.jpg`} preview={false} onClick={() => open(url)} />
                        :
                        <Image alt={codigo} className='bdRoundBorder' src={`/assets/images/hall/${codigo}.jpg`} preview={false} />
            }
        </Col >
    }

    const renderDetalle = ({ codigo, nombre, descripcion }) => <div key={codigo}>
        <Typography.Title level={3} style={{ fontFamily: 'Segoe Script', textTransform: 'uppercase', color: 'goldenrod' }}>
            {nombre}
        </Typography.Title>
        <Typography.Paragraph>
            <span style={{ letterSpacing: '1px' }}>
                {descripcion}
            </span>
        </Typography.Paragraph>
    </div>

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        return (
            <Layout.Content>
                <Row justify='center'>
                    <Col xs={20} sm={16} md={14} lg={12} xl={10} xxl={8}>
                        {
                            imagen ?
                                <Image alt='Marcell Rodriguez' src={`/assets/images/hall/${imagen}`} preview={false} />
                                :
                                <Typography.Title level={1} style={{ color: '#ffffff', textTransform: 'uppercase', letterSpacing: '1px' }}>
                                    {titulo}
                                </Typography.Title>
                        }
                    </Col>
                </Row>
                <Row justify='center'>
                    <Col xs={20} sm={10} md={10} lg={12} xl={10} xxl={8}></Col>
                    <Col xs={20} sm={10} md={12} lg={12} xl={10} xxl={10}>
                        <Row justify="center" gutter={[15, 15]}>
                            {
                                items.map(hi => renderHallItem(hi))
                            }
                        </Row>
                    </Col>
                </Row>
                <Row justify='center'>
                    <Col xs={20} sm={20} md={22} lg={24} xl={20} xxl={18} style={{ padding: '15px 0' }}>
                        <Card bordered style={{ background: 'rgba(255, 255, 255, 0.85)' }}>
                            {
                                detalle.map(d => renderDetalle(d))
                            }
                        </Card>
                    </Col>
                </Row>
                <Row justify='center'>
                    <Col>
                        <div className='bdNav'>
                            <ul>
                                {
                                    botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                        {titulo}
                                        <span></span><span></span><span></span><span></span>
                                    </li>)
                                }
                            </ul>
                        </div>
                    </Col>
                </Row>
            </Layout.Content >
        )
    }

    return (
        <Layout.Content>
            <Loader />
        </Layout.Content >
    )

}

export default HallFama