import { storage } from "./constants"
export const getDatosAutenticacion = () => (localStorage.getItem(storage.ADATA) && JSON.parse(localStorage.getItem(storage.ADATA))) || {}
export const getDatosGenerales = () => (localStorage.getItem(storage.DATOSGENERALES) && JSON.parse(localStorage.getItem(storage.DATOSGENERALES))) || {}
export const getUserName = () => getDatosAutenticacion().user && getDatosAutenticacion().user.login ? getDatosAutenticacion().user.login : ''
export const getUser = () => getDatosAutenticacion().user && getDatosAutenticacion().user.code ? getDatosAutenticacion().user.code : ''
export const getName = () => getDatosAutenticacion().user && getDatosAutenticacion().user.name ? getDatosAutenticacion().user.name : ''
export const getToken = () => getDatosAutenticacion().token || ''
export const getOptions = () => getDatosAutenticacion().options || []
export const getIsAdm = () => getDatosAutenticacion().user && getDatosAutenticacion().user.role ? getDatosAutenticacion().user.role === "1" : false
export const getIsSup = () => getDatosAutenticacion().user && getDatosAutenticacion().user.role ? getDatosAutenticacion().user.role === "2" : false
export const getIsUser = () => getDatosAutenticacion().user && getDatosAutenticacion().user.role ? getDatosAutenticacion().user.role === "3" : false
export const getAutenticado = () => getDatosAutenticacion().user && getDatosAutenticacion().user.authenticated ? getDatosAutenticacion().user.authenticated : false;
export const getPaises = () => getDatosGenerales().paises || []
export const getPerfiles = () => getDatosGenerales().perfiles || []
export const getSupervisores = () => getDatosGenerales().supervisores || []
export const eliminarSesion = () => {
    localStorage.removeItem(storage.ADATA)
    localStorage.removeItem(storage.DATOSGENERALES)
}
export const cerrarSesion = (navigate) => {
    eliminarSesion()
    navigate.push('/login')
}
export const setDatosAutenticacion = (datosAutenticacion) => localStorage.setItem(storage.ADATA, JSON.stringify(datosAutenticacion))
export const setDatosGenerales = (datosGenerales) => localStorage.setItem(storage.DATOSGENERALES, JSON.stringify(datosGenerales))