import React from 'react'
import { Spin, Typography } from 'antd'
import * as anticons from '@ant-design/icons'

const Loader = () => {
    return (
        <div
            style={{
                textAlign: "center",
            }}
        >
            <Spin
                size="large"
                indicator={
                    <anticons.LoadingOutlined
                        style={{
                            fontSize: 64,
                            color: "goldenrod",
                        }}
                    />
                }
            />
            <Typography.Paragraph
                strong
                style={{
                    fontSize: "1.5em",
                    color: "#666666",
                    letterSpacing: "0.1em",
                }}
            >
                <br />
                Espere un momento...
            </Typography.Paragraph>
        </div>
    )
}

export default Loader