import React, { useState, useEffect } from 'react'
import { useParams, useHistory } from 'react-router-dom'
import { Col, Row, Image, Typography } from 'antd'
import { toast } from "react-hot-toast";
import Loader from './../components/Loader'

import { getUserName } from "../utilities";

import UsersService from "../service/UsersService";
import AdministrativoService from './../service/AdministrativoService'
const usersService = new UsersService();
const administrativoService = new AdministrativoService()

const Administrativo = () => {
    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const { codigo } = useParams()
    const administrativo = administrativoService.gets()
    const detalleAdministrativo = administrativoService.get(codigo)

    const go = (url) => {
        navigate.push(`/${url}`);
    }

    const renderAdministrativo = ({ id, nombre, imagen, url, video, visible }) => <Col key={id} xs={24} sm={20} md={18} lg={18} xl={14} xxl={14} style={{ textAlign: 'center' }}>
        <Typography.Title level={4} style={{ color: '#000000', letterSpacing: '1px', textTransform: 'uppercase' }}>
            {nombre}
        </Typography.Title>
        {
            visible ?
                <div style={{ height: '350px' }}>
                    {
                        video
                    }
                </div> :
                <Image alt={nombre} className='bdRoundBorder' src={`/assets/images/administrativo/${imagen}`} preview={false} />
        }
    </Col>

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        if (detalleAdministrativo && detalleAdministrativo.id) {
            const { imagenTitulo } = administrativo
            const { titulo, botones, items } = detalleAdministrativo
            return (
                <>
                    <Row justify='center' gutter={[20, 20]}>
                        <Col xs={22} sm={20} md={14} lg={12} xl={10} xxl={8}>
                            <Row>
                                <Col span={24}>
                                    <Image alt={titulo} src={`/assets/images/administrativo/${imagenTitulo}`} preview={false} />
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row justify='center' gutter={[20, 20]}>
                        <Col xs={22} sm={20} md={18} lg={18} xl={18} xxl={18}>
                            <Row>
                                {
                                    items && items.length > 0 &&
                                    <Col span={24}>
                                        <Row gutter={[15, 15]} justify='center'>
                                            {
                                                items.map(s => renderAdministrativo(s))
                                            }
                                        </Row>
                                    </Col>
                                }
                            </Row>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                            <div className='bdNav'>
                                <ul>
                                    {
                                        botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                            {titulo}
                                            <span></span><span></span><span></span><span></span>
                                        </li>)
                                    }
                                </ul>
                            </div>
                            {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                        </Col>
                    </Row>
                </>
            )
        }
    }

    return (
        <>
            <Loader />
        </>
    )
}

export default Administrativo