import React, { useState, useEffect } from 'react'
import { Row, Col, Space, Button, Typography } from 'antd'
import * as anticons from '@ant-design/icons'

import { Document, Page, pdfjs } from 'react-pdf'
import 'react-pdf/dist/cjs/Page/AnnotationLayer.css'

const LectorPdf = ({ fileName }) => {

    const [numPages, setNumPages] = useState(null);
    const [pageNumber, setPageNumber] = useState(1);

    const onDocumentLoadSuccess = ({ numPages }) => {
        setNumPages(numPages)
    }

    useEffect(() => {
        pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`
    })

    return <div>
        <Row justify={'center'}>
            <Col>
                <Space>
                    <Button onClick={() => setPageNumber(pageNumber - 1)} disabled={pageNumber === 1} icon={<anticons.ArrowLeftOutlined />}>Anterior</Button>
                    <Button onClick={() => setPageNumber(pageNumber + 1)} disabled={pageNumber === numPages} icon={<anticons.ArrowRightOutlined />}>Siguiente</Button>
                </Space>
            </Col>
        </Row>
        <Row justify={'center'} style={{ padding: '10px' }}>
            <Col>
                <Document file={{ url: `/assets/files/${fileName}` }} onLoadSuccess={onDocumentLoadSuccess}>
                    <Page pageNumber={pageNumber} renderTextLayer={false} />
                </Document>
            </Col>
        </Row>
        <Row justify={'center'}>
            <Col>
                <Space>
                    <Button onClick={() => setPageNumber(pageNumber - 1)} disabled={pageNumber === 1} icon={<anticons.ArrowLeftOutlined />}>Anterior</Button>
                    <Button onClick={() => setPageNumber(pageNumber + 1)} disabled={pageNumber === numPages} icon={<anticons.ArrowRightOutlined />}>Siguiente</Button>
                </Space>
            </Col>
        </Row>
        <Row justify={'center'}>
            <Col>
                <Typography.Paragraph strong italic>
                    Pgina {pageNumber} de {numPages}
                </Typography.Paragraph>
            </Col>
        </Row>
    </div>
}

export default LectorPdf