import React, { useEffect, useState } from 'react'
import { useHistory, useParams } from 'react-router-dom'

import { Form, Row, Col, Input, Select, Button, Typography, Switch, Space, Image, Spin, Popconfirm, Card, Alert } from 'antd'
import * as anticons from '@ant-design/icons'
import toast from 'react-hot-toast'
import Loader from './../components/Loader'

import { getUserName, getPaises, getPerfiles, getSupervisores, getIsAdm, getIsSup, getUser } from '../utilities'
import UsersService from '../service/UsersService'
const usersService = new UsersService()

const User = () => {

    const navigate = useHistory()
    const { id } = useParams()
    const [cargar, setCargar] = useState(true)
    const [cargando, setCargando] = useState(false)
    const [visualizar, setVisualizar] = useState(false);
    const [cargarUser, setCargarUser] = useState(false)
    const [guardando, setGuardando] = useState(false)
    const [message, setMessage] = useState(null);
    const [user, setUser] = useState({})

    const go = () => {
        navigate.push('/users')
    }

    const guardar = async (datos) => {
        const user = { ...datos, ...{ activo: datos.activo === true ? '1' : '0', id, responsable: getUserName() } }
        setGuardando(true)
        setMessage(null)
        try {
            const { data } = await usersService.put({ navigate, user })
            const { detalle, estado } = data
            if (estado)
                toast.success(detalle)
            else
                toast.error(detalle)
        } catch ({ response }) {
            const { data } = response
            setMessage(data.retorno.indexOf('IDX_CorreoUnico') > 0 ? `El correo ${user.correo} ya se encuentra registrado` : data.retorno.indexOf('IDX_PaisIdentificacionUnico') > 0 ? `La identificacin ${user.identificacion} ya se encuentra registrada` : data.detalle)
        } finally {
            setGuardando(false)
        }
    }

    const aprobarSolicitud = (user) => {
        updateSolicitud({ ...user, estadoRespuesta: true })
    }

    const rechazarSolicitud = (user) => {
        updateSolicitud({ ...user, estadoRespuesta: false })
    }

    const updateSolicitud = async (datos) => {
        const user = { ...datos, ...{ id, responsable: getUserName() } }
        setGuardando(true)
        setMessage(null)
        try {
            const { data } = await usersService.updateRequest({ navigate, user })
            const { detalle, estado } = data
            if (estado)
                toast.success(detalle)
            else
                toast.error(detalle)
            go()
        } catch ({ response }) {
            const { data } = response
            setMessage(data.detalle)
        } finally {
            setGuardando(false)
        }
    }

    const get = async () => {
        setCargando(true)
        try {
            const { data } = await usersService.get({ navigate, id, responsable: getUserName() })
            const usuario = { ...data[0] }
            const usuarioEstado = { ...usuario, ...{ activo: usuario.activo === '1' ? true : false } }
            setUser(usuarioEstado)
        } catch {
        } finally {
            setCargarUser(false)
            setCargando(false)
        }
    }

    const sendResetPassword = async (correo) => {
        setGuardando(true)
        const user = {
            correo,
            responsable: getUserName()
        }
        try {
            const { data } = await usersService.sendResetPassword({ user })
            const { detalle, estado } = data
            if (estado)
                toast.success(detalle)
            else
                toast.error(detalle)
        } catch ({ response }) {
            const { data } = response
            toast.error(data.detalle);
        } finally {
            setGuardando(false)
        }
    }

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setCargarUser(true);
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setCargarUser(true);
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    useEffect(() => {
        if (cargarUser && !cargando) {
            get()
        }
    })

    if (cargar === false && cargando === false && visualizar === true) {
        return (
            <>
                <div
                    style={{
                        color: "#333333",
                        padding: "20px 0",
                    }}
                >
                    <Image
                        alt="BYDZYNE"
                        src="/assets/images/logoBlack.png"
                        style={{ height: "80px", width: "80px" }}
                    />
                    <span
                        style={{
                            marginLeft: "20px",
                            fontSize: "1.5em",
                            letterSpacing: "3px",
                            fontWeight: "bold",
                        }}
                    >
                        BYDZYNE
                    </span>
                </div>
                <Row justify='center'>
                    <Col xs={24} sm={20} md={18} lg={16} xl={12} xxl={10} style={{ textAlign: 'center' }}>
                        <Typography.Title level={2}>
                            Actualizar Usuario
                        </Typography.Title>
                    </Col>
                </Row>
                <Row justify='center'>
                    <Col xs={24} sm={20} md={18} lg={16} xl={12} xxl={10}>
                        <Card
                            bordered
                            style={{
                                backgroundColor: 'rgba(255,255,255, 0.9)',
                                padding: '10px 10px 0 10px'
                            }}
                        >
                            {
                                user.id ?
                                    <Form
                                        name='usuario'
                                        layout={'vertical'}
                                        initialValues={user}
                                        onFinish={guardar}
                                        disabled={guardando}
                                    >
                                        <Row justify='end' style={{ paddingBottom: '15px' }}>
                                            <Col>
                                                <Button type='link' onClick={() => go()} disabled={guardando || cargar}
                                                    icon={<anticons.ArrowLeftOutlined />}
                                                >Regresar</Button>
                                            </Col>
                                        </Row>
                                        <Row gutter={10}>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="identificacion"
                                                    label={
                                                        <Typography.Text>
                                                            Identificacin
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Ingrese la identificacin',
                                                        },
                                                    ]}
                                                >
                                                    <Input />
                                                </Form.Item>
                                            </Col>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="nombres"
                                                    label={
                                                        <Typography.Text>
                                                            Nombres
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Ingrese los nombres',
                                                        },
                                                    ]}>
                                                    <Input />
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row gutter={10}>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="apellidoPaterno"
                                                    label={
                                                        <Typography.Text>
                                                            Apellido Paterno
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Ingrese el apellido paterno',
                                                        },
                                                    ]}>
                                                    <Input />
                                                </Form.Item>
                                            </Col>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="apellidoMaterno"
                                                    label={
                                                        <Typography.Text>
                                                            Apellido Materno
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Ingrese el apellido materno',
                                                        },
                                                    ]}>
                                                    <Input />
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row gutter={10}>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="idPais"
                                                    label={
                                                        <Typography.Text>
                                                            Pais
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Seleccione el pais',
                                                        },
                                                    ]}>
                                                    <Select
                                                        value={user.idPais}
                                                        showSearch
                                                        options={getPaises()}
                                                        fieldNames={{
                                                            label: 'nombre',
                                                            value: 'id'
                                                        }}
                                                        optionFilterProp="nombre"
                                                    />
                                                </Form.Item>
                                            </Col>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="telefono"
                                                    label={
                                                        <Typography.Text>
                                                            Telfono
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Ingrese el telfono',
                                                        },
                                                    ]}>
                                                    <Input />
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row gutter={10}>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="correo"
                                                    label={
                                                        <Typography.Text>
                                                            Correo
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Ingrese el correo',
                                                        },
                                                    ]}>
                                                    <Input />
                                                </Form.Item>
                                            </Col>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="idPerfil"
                                                    label={
                                                        <Typography.Text>
                                                            Perfil
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Seleccione el perfil',
                                                        },
                                                    ]}>
                                                    <Select
                                                        value={user.idPerfil}
                                                        showSearch
                                                        options={getPerfiles()}
                                                        fieldNames={{
                                                            label: 'nombres',
                                                            value: 'id'
                                                        }}
                                                        optionFilterProp="nombres"
                                                        disabled={!getIsAdm()}
                                                    />
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row gutter={10}>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="idResponsable"
                                                    label={
                                                        <Typography.Text>
                                                            Presidente
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Seleccione el presidente responsable',
                                                        },
                                                    ]}>
                                                    <Select
                                                        value={user.idResponsable}
                                                        showSearch
                                                        options={getIsAdm() === true ? getSupervisores() : getIsSup() === true ? getSupervisores().filter(s => s.id === getUser()) : []}
                                                        fieldNames={{
                                                            label: 'nombres',
                                                            value: 'id'
                                                        }}
                                                        optionFilterProp="nombres"
                                                        disabled={!getIsAdm()}
                                                    />
                                                </Form.Item>
                                            </Col>
                                            <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                                <Form.Item
                                                    name="activo"
                                                    valuePropName="checked"
                                                    label={
                                                        <Typography.Text>
                                                            Activo
                                                        </Typography.Text>
                                                    }
                                                    rules={[
                                                        {
                                                            required: true,
                                                            message: 'Especifique el estado',
                                                        },
                                                    ]}
                                                >
                                                    <Switch defaultChecked checkedChildren="S" unCheckedChildren="No"
                                                    />
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Row gutter={10}>
                                            <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24}>
                                                <Form.Item
                                                    label={
                                                        (user.eliminado === '1' && user.existeSolicitudPendiente === 1) || (user.fechaRespuesta !== null && user.estadoRespuesta !== null) ?
                                                            <Space direction='vertical'>
                                                                <Typography.Text strong>
                                                                    Solicitud de Acceso
                                                                </Typography.Text>
                                                                <Typography.Text type='secondary'>
                                                                    {`${user.estadoRespuesta === true ? 'Aprobada el ' : user.estadoRespuesta === false ? 'Rechazada el ' : ''}`}
                                                                    {`${user.fechaRespuesta !== null ? new Date(user.fechaRespuesta).toLocaleString() : ''}`}
                                                                </Typography.Text>
                                                            </Space>
                                                            : <></>
                                                    }
                                                >
                                                    {
                                                        user.eliminado === '1' && user.existeSolicitudPendiente === 1 &&
                                                        <Space>
                                                            <Popconfirm
                                                                title="Aprobar Solicitud"
                                                                icon={<anticons.QuestionCircleFilled style={{ color: '#336699' }} />}
                                                                description="Seguro(a) que desea aprobar la solicitud de acceso?"
                                                                onConfirm={() => aprobarSolicitud(user)}
                                                                okText="Si"
                                                                cancelText="No"
                                                                okButtonProps={{
                                                                    loading: guardando,
                                                                }}
                                                            >
                                                                <Button type='default' disabled={guardando || cargar} loading={guardando}
                                                                    icon={<anticons.LikeFilled />}
                                                                >Aprobar</Button>
                                                            </Popconfirm>
                                                            <Popconfirm
                                                                title="Rechazar Solicitud"
                                                                icon={<anticons.QuestionCircleFilled style={{ color: '#336699' }} />}
                                                                description="Seguro(a) que desea rechazar la solicitud de acceso?"
                                                                onConfirm={() => rechazarSolicitud(user)}
                                                                okText="Si"
                                                                cancelText="No"
                                                                okButtonProps={{
                                                                    loading: guardando,
                                                                }}
                                                            >
                                                                <Button type='default' danger disabled={guardando || cargar} loading={guardando}
                                                                    icon={<anticons.DislikeFilled />}
                                                                >Rechazar</Button>
                                                            </Popconfirm>
                                                        </Space>
                                                    }
                                                </Form.Item>
                                            </Col>
                                        </Row>
                                        <Form.Item>
                                            <Row justify='center'>
                                                <Col>
                                                    {
                                                        message && <Alert message={message} type='warning' />
                                                    }
                                                </Col>
                                            </Row>
                                            <Row justify='end' style={{ paddingTop: '15px' }}>
                                                <Col>
                                                    <Space>
                                                        <Button type="primary" htmlType='submit' disabled={guardando || cargar} loading={guardando}
                                                            icon={<anticons.SaveFilled />}
                                                        >
                                                            {guardando ? 'Guardando...' : 'Guardar'}
                                                        </Button>
                                                        {
                                                            user.eliminado === '0' && user.existeSolicitudPendiente === 0 &&
                                                            <Popconfirm
                                                                title="Restablecer contrasea"
                                                                icon={<anticons.QuestionCircleFilled style={{ color: '#336699' }} />}
                                                                description="Desea enviar un correo al usuario para restablecer su contrasea?"
                                                                onConfirm={() => sendResetPassword(user.correo)}
                                                                okText="Si"
                                                                cancelText="No"
                                                                okButtonProps={{
                                                                    loading: guardando,
                                                                }}
                                                            >
                                                                <Button type='default' disabled={guardando || cargar} loading={guardando}
                                                                    icon={<anticons.SendOutlined />}
                                                                >Restablecer Contrasea</Button>
                                                            </Popconfirm>
                                                        }
                                                    </Space>
                                                </Col>
                                            </Row>
                                            <Row justify='end' style={{ paddingTop: '15px' }}>
                                                <Col>
                                                    <Button type='link' onClick={() => go()} disabled={guardando || cargar}
                                                        icon={<anticons.ArrowLeftOutlined />}
                                                    >Regresar</Button>
                                                </Col>
                                            </Row>
                                        </Form.Item>
                                    </Form>
                                    :
                                    <div style={{
                                        textAlign: 'center'
                                    }} >
                                        <Spin
                                            size='large'
                                            indicator={
                                                <anticons.LoadingOutlined
                                                    style={{
                                                        fontSize: 32
                                                    }} />
                                            }
                                        />
                                        <Typography.Paragraph>
                                            <br />Espere un momento...
                                        </Typography.Paragraph>
                                    </div>
                            }
                        </Card>
                    </Col >
                </Row >
            </>
        )
    }


    return (
        <>
            <Loader />
        </>
    )

}

export default User