import React, { useEffect, useState } from "react";
import { Route, withRouter, useLocation } from "react-router-dom";
import App from "./App";
import Home from "./pages/Home";
import Ingresar from "./pages/Ingresar";
import Register from "./pages/Register";

export const ctx = React.createContext();

const AppWrapper = (props) => {
  let location = useLocation();
  const rutas = location.pathname.split("/");
  const estilo = rutas[rutas.length - 1];

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location]);

  const [colorScheme, setColorScheme] = useState("light");

  switch (props.location.pathname) {
    case "/login":
      return (
        <Route
          path="/login"
          render={() => <Ingresar colorScheme={colorScheme} />}
        />
      );
    case "/register":
      return (
        <Route
          path="/register"
          render={() => <Register colorScheme={colorScheme} />}
        />
      );
    case "/":
      return <Route path="/" render={() => <Home />} />;
    default:
      return <App setColorScheme={setColorScheme} fondo={estilo} />;
  }
};

export default withRouter(AppWrapper);
