import React, { useState, useEffect } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { Tooltip, Col, Image, Layout, Row } from 'antd'
import { toast } from "react-hot-toast";
import Loader from './../components/Loader'

import { getUserName } from "../utilities";

import UsersService from "../service/UsersService";
import PresidenteService from './../service/PresidenteService'
const usersService = new UsersService();
const presidenteService = new PresidenteService()

const Presidente = () => {

    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const { codigo } = useParams()
    const { nombre, stars, imagen, imagenArticulo, linkArticulo } = presidenteService.get(codigo)
    const botones = presidenteService.getBotones(stars)
    const numStars = stars === '2' ? 'two' : 'one'
    const go = (url) => navigate.push(url)
    const irArticulo = () => window.open(linkArticulo)

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        return (
            <Layout.Content>
                <Row justify='center'>
                    <Col xs={22} sm={22} md={22} lg={18} xl={14} xxl={12}>
                        <Image alt={nombre} src={`/assets/images/presidentes/${numStars}/${codigo}Titulo.png`} preview={false} className="bdRoundBorder" />
                    </Col>
                </Row>
                <Row justify='center' gutter={[15, 15]}>
                    <Col xs={22} sm={22} md={10} lg={8} xl={6} xxl={6}>
                        <Image alt={nombre} src={imagen} preview={false} className="bdRoundBorder" />
                    </Col>
                    <Col xs={22} sm={22} md={12} lg={12} xl={12} xxl={12}>
                        <Tooltip title="Clic para ver artculo BFH">
                            <Image alt={nombre} src={imagenArticulo} preview={false} style={{ cursor: 'pointer' }} onClick={irArticulo} className="bdRoundBorder" />
                        </Tooltip>
                    </Col>
                </Row>
                <Row>
                    <Col span={24} style={{ textAlign: 'center', padding: '20px 0' }}>
                        <div className='bdNav'>
                            <ul>
                                {
                                    botones && botones.subItems.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                        {titulo}
                                        <span></span><span></span><span></span><span></span>
                                    </li>)
                                }
                            </ul>
                        </div>
                    </Col>
                </Row>
            </Layout.Content>
        )
    }

    return (
        <Layout.Content>
            <Loader />
        </Layout.Content >
    )
}

export default Presidente