import axios from 'axios'
import axiosInstance from '../utilities/axiosInstance'
import { urls } from '../utilities/constants'

export class UsersService {
    gets = ({ navigate, responsable, filtros }) => axiosInstance(navigate).get(`${urls.GETS_USERS}${responsable}`, { params: { ...filtros } })
    get = ({ navigate, id, responsable }) => axiosInstance(navigate).get(`${urls.GET_USER}${id}/${responsable}`)
    put = ({ navigate, user }) => axiosInstance(navigate).put(`${urls.PUT_USER}`, user)
    set = ({ navigate, user }) => axiosInstance(navigate).post(`${urls.PUT_USER}`, user)
    sendResetPassword = ({ user }) => axios.post(urls.SEND_RESET_PASSWORD, user)
    getResetPassword = ({ datos }) => axios.post(urls.GET_RESET_PASSWORD, datos)
    resetPassword = ({ user }) => axios.post(urls.RESET_PASSWORD, user)
    updateRequest = ({ navigate, user }) => axiosInstance(navigate).post(urls.UPDATE_REQUEST, user)
    isAuthenticated = ({ navigate, responsable }) => axiosInstance(navigate).get(`${urls.GET_AUTHENTICATION}/${responsable}`)
}

export default UsersService