import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Button, Col, Row, Image, Typography } from 'antd'
import * as anticons from '@ant-design/icons'
import { toast } from "react-hot-toast";
import Loader from './../components/Loader'

import { getUserName } from "../utilities";

import UsersService from "../service/UsersService";
import FlyerService from './../service/FlyerService'
const usersService = new UsersService();
const flyerService = new FlyerService()

const Flyers = () => {
    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const flyers = flyerService.gets()

    const go = (url) => {
        navigate.push(`/${url}`);
    }

    const renderFlyer = ({ id, titulo, nombre, imagen, imagenDescarga, url }) => <Col key={id} xs={24} sm={12} md={12} lg={12} xl={8} xxl={8} style={{ textAlign: 'center' }}>
        {/* <Typography.Title level={4} style={{ color: '#ffffff', letterSpacing: '1px' }}>
            <blockquote>
                {titulo}
            </blockquote>
        </Typography.Title> */}
        <Image alt={nombre} className='bdRoundBorder' src={`/assets/images/planAccion/flyers/${imagen}`} />
        <br /><br />
        <Button type='primary' target='_blank' icon={<anticons.DownloadOutlined />} href={`/assets/images/planAccion/flyers/${imagenDescarga}`}>Descargar</Button>
    </Col>

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        if (flyers && flyers.id) {
            const { codigo, titulo, imagenTitulo, subTitulo, descripcion, botones, items } = flyers
            return (
                <>
                    <Row justify='center' gutter={[20, 20]}>
                        <Col xs={22} sm={20} md={14} lg={12} xl={10} xxl={8}>
                            {
                                imagenTitulo ?
                                    <Image alt={titulo} src={`/assets/images/planAccion/${codigo}/${imagenTitulo}`} preview={false} />
                                    :
                                    <>
                                        <Typography.Title level={1}>
                                            {titulo}
                                        </Typography.Title>
                                        <Typography.Title level={2}>
                                            {subTitulo}
                                        </Typography.Title>
                                    </>
                            }
                        </Col>
                    </Row>
                    <Row justify='center' gutter={[20, 20]}>
                        <Col xs={22} sm={20} md={16} lg={16} xl={16} xxl={16}>
                            <Row justify='center'>
                                <Col span={24}>
                                    <Typography.Title level={2} style={{ color: 'goldenrod', textAlign: 'center', textTransform: 'uppercase' }}>
                                        {subTitulo}
                                    </Typography.Title>
                                </Col>
                                {
                                    items && items.length > 0 &&
                                    <Col span={24} style={{ padding: '20px 0 30px 0' }}>
                                        <Row gutter={[15, 15]} justify='center'>
                                            {
                                                items.map(s => renderFlyer(s))
                                            }
                                        </Row>
                                    </Col>
                                }
                                <Col span={24}>
                                    <Typography.Title level={3} style={{ color: 'goldenrod', textAlign: 'center', textTransform: 'uppercase' }}>
                                        {descripcion}
                                    </Typography.Title>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                            <div className='bdNav'>
                                <ul>
                                    {
                                        botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                            {titulo}
                                            <span></span><span></span><span></span><span></span>
                                        </li>)
                                    }
                                </ul>
                            </div>
                            {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                        </Col>
                    </Row >
                </>
            )
        }
    }

    return (
        <>
            <Loader />
        </>
    )

}

export default Flyers