import React, { useState, useEffect } from 'react'
import { useParams, useHistory } from 'react-router-dom'
import { Button, Col, Row, Image, Typography, Tooltip, Modal } from 'antd'
import LectorPdf from '../components/LectorPdf'
import { toast } from "react-hot-toast";
import Loader from './../components/Loader'

import { getUserName } from "../utilities";

import UsersService from "../service/UsersService";
import PlanificacionService from './../service/PlanificacionService'
const planificacionService = new PlanificacionService()
const usersService = new UsersService();

const Planificacion = () => {

    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const { paso } = useParams()
    const [archivoActual, setArchivoActual] = useState(null)
    const [mostrarLector, setMostrarLector] = useState(false);
    const detallePaso = planificacionService.get(paso)

    const go = (url) => {
        navigate.push(`/${url}`);
    }

    const open = (url) => window.open(url)

    const renderRed = ({ id, nombre, imagen, url }) => <Col key={id} span={4} style={{ textAlign: 'center' }}>
        <Tooltip title={nombre}>
            <Image alt={nombre} className='bdPuntero' src={`/assets/images/planAccion/${paso}/${imagen}`} onClick={() => open(url)} preview={false} />
        </Tooltip>
    </Col>

    const renderDocumento = ({ id, codigo, titulo, imagen, fileName, links, mostrarTitulo }) =>
        <Col key={id} xs={12} sm={12} md={12} lg={12} xl={12} xxl={12} style={{ textAlign: 'center', padding: '10px 10px' }}>
            {
                mostrarTitulo &&
                <Typography.Title level={5}>
                    {titulo}
                </Typography.Title>
            }
            <Image alt={codigo} src={`/assets/images/planAccion/${paso}/${imagen}`} className='bdRoundBorder' style={{ cursor: 'pointer' }} onClick={() => verArchivo({ titulo, fileName })} preview={false} />
            <Row>
                {
                    links.map(l => <Col key={l.titulo} span={24} style={{ textAlign: 'center', padding: '10px 0px 0px 0px' }}>
                        {
                            l.url.indexOf('https://') >= 0 ? 
                            <Button type={l.tipo} target='_blank' icon={l.icon} style={l.estilo} href={`${l.url}`}>{l.titulo}</Button>
                            : 
                            <Button type={l.tipo} target='_blank' icon={l.icon} style={l.estilo} href={`/assets/files/${l.url}`}>{l.titulo}</Button>
                        }
                    </Col>)
                }
            </Row>
        </Col >

    const renderApp = ({ id, codigo, imagen, url }) => <Col key={id} xs={8} sm={6} md={6} lg={6} xl={6} xxl={5} style={{ textAlign: 'center' }}>
        <Image alt={codigo} src={`/assets/images/planAccion/${paso}/${imagen}`} style={{ cursor: 'pointer' }} preview={false} onClick={() => open(url)} />
    </Col>

    const verArchivo = (archivo) => {
        setArchivoActual(archivo)
        setMostrarLector(true)
    }

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        if (detallePaso && detallePaso.id) {
            const { codigo, titulo, imagenTitulo, subTitulo, video, botones, descripcion, redes, documentos, apps, items } = detallePaso
            if (codigo === 'pasoCero' || codigo === 'pasoUno' || codigo === 'pasoDos') {
                return (
                    <>
                        <Row justify='center' gutter={[20, 20]}>
                            <Col xs={22} sm={20} md={14} lg={12} xl={10} xxl={8}>
                                <Row>
                                    <Col span={24}>
                                        {
                                            imagenTitulo ?
                                                <Image alt={titulo} src={`/assets/images/planAccion/${codigo}/${imagenTitulo}`} preview={false} />
                                                :
                                                <>
                                                    <Typography.Title level={1}>
                                                        {titulo}
                                                    </Typography.Title>
                                                    <Typography.Title level={2}>
                                                        {subTitulo}
                                                    </Typography.Title>
                                                </>
                                        }
                                    </Col>
                                </Row>
                            </Col>
                        </Row>
                        <Row justify='center' gutter={[20, 20]}>
                            <Col xs={22} sm={20} md={16} lg={14} xl={12} xxl={12}>
                                <Row>
                                    <Col span={24}>
                                        {/* <Image alt={subTitulo} className='bdRoundBorder' src={`/assets/images/planAccion/${codigo}/${imagen}`} preview={false} onClick={() => open(url)} /> */}
                                        <div style={{ height: '450px' }}>
                                            {
                                                video
                                            }
                                        </div>
                                    </Col>
                                    <Col span={24} style={{ textAlign: 'center' }}>
                                        {
                                            codigo === 'pasoUno' ?
                                                <Typography.Paragraph style={{ fontSize: '1.5em', color: '#ffffff' }}>
                                                    <blockquote>
                                                        {descripcion}
                                                    </blockquote>
                                                </Typography.Paragraph>
                                                :
                                                <Typography.Paragraph style={{ fontSize: '1.5em' }}>
                                                    <blockquote>
                                                        {descripcion}
                                                    </blockquote>
                                                </Typography.Paragraph>
                                        }
                                    </Col>
                                    {
                                        redes && redes.length > 0 &&
                                        <Col span={24}>
                                            <Row justify='center' style={{ textAlign: 'center' }}>
                                                <Col xs={20} sm={18} md={16} lg={14} xl={12} xxl={10} >
                                                    <Row>
                                                        {
                                                            redes.map(r => renderRed(r))
                                                        }
                                                    </Row>
                                                </Col>
                                            </Row>
                                        </Col>
                                    }
                                </Row>
                            </Col>
                            {
                                documentos && documentos[0] &&
                                <Col xs={20} sm={12} md={8} lg={8} xl={6} xxl={6}>
                                    <Row gutter={[20, 30]}>
                                        {/* <Col span={24} style={{ textAlign: 'center', letterSpacing: '1px' }}>
                                        {
                                            codigo === 'pasoUno' ?
                                                <Typography.Title level={3} style={{ color: '#ffffff' }}>
                                                    {documentos[0].titulo}
                                                </Typography.Title>
                                                :
                                                <Typography.Title level={3}>
                                                    {documentos[0].titulo}
                                                </Typography.Title>
                                        }
                                    </Col> */}
                                        <Col span={24}>
                                            <Row>
                                                {
                                                    documentos[0].items.map(d => renderDocumento(d))
                                                }
                                            </Row>
                                        </Col>
                                        {
                                            apps && apps[0] && <>
                                                <Col span={24} style={{ textAlign: 'center', letterSpacing: '1px' }}>
                                                    <Typography.Title level={4}>
                                                        {apps[0].titulo}
                                                    </Typography.Title>
                                                </Col>
                                                <Col span={24}>
                                                    <Row justify='center'>
                                                        {
                                                            apps[0].items.map(a => renderApp(a))
                                                        }
                                                    </Row>
                                                </Col>
                                                <Col span={24} style={{ textAlign: 'center', letterSpacing: '1px' }}>
                                                    <Typography.Title level={4}>
                                                        {apps[1].titulo}
                                                    </Typography.Title>
                                                </Col>
                                                <Col span={24}>
                                                    <Row justify='center'>
                                                        {
                                                            apps[1].items.map(a => renderApp(a))
                                                        }
                                                    </Row>
                                                </Col>
                                            </>
                                        }
                                    </Row>
                                </Col>
                            }
                        </Row>
                        <Row>
                            <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                                <div className='bdNav'>
                                    <ul>
                                        {
                                            botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                                {titulo}
                                                <span></span><span></span><span></span><span></span>
                                            </li>)
                                        }
                                    </ul>
                                </div>
                                {/* <Space>
                                {
                                    botones.map(b =>
                                        <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                    )
                                }
                            </Space> */}
                            </Col>
                        </Row >
                        {
                            archivoActual && archivoActual.titulo && archivoActual.fileName &&
                            <Modal
                                title={archivoActual.titulo}
                                style={{
                                    top: 15
                                }}
                                bodyStyle={
                                    {
                                        overflowX: 'scroll',
                                        overflowY: 'scroll'
                                    }
                                }
                                okText={"Aceptar"}
                                cancelText={"Cerrar"}
                                open={mostrarLector}
                                onOk={() => setMostrarLector(false)}
                                onCancel={() => setMostrarLector(false)}
                                footer={null}
                                width={'100%'}
                            >
                                <LectorPdf fileName={archivoActual.fileName} />
                            </Modal>
                        }
                    </>
                )
            } else if (codigo === 'pasoTres' || codigo === 'pasoCuatro') {
                return (
                    <>
                        <Row justify='center'>
                            <Col xs={22} sm={20} md={14} lg={12} xl={10} xxl={8}>
                                {
                                    imagenTitulo ?
                                        <Image alt={titulo} src={`/assets/images/planAccion/${codigo}/${imagenTitulo}`} preview={false} />
                                        :
                                        <Typography.Title level={1}>
                                            {titulo}
                                        </Typography.Title>
                                }
                            </Col>
                        </Row>
                        <Row justify='center'>
                            <Col xs={22} sm={22} md={20} lg={18} xl={12} xxl={12}>
                                {
                                    <Typography.Title level={2} style={{ color: '#cccccc', textAlign: 'center' }}>
                                        {subTitulo}
                                    </Typography.Title>
                                }
                            </Col>
                        </Row>
                        {
                            items.map(i => {
                                const { id, subTitulo, video } = i
                                return (
                                    <div key={id}>
                                        <Row justify='center'>
                                            <Col xs={22} sm={22} md={20} lg={18} xl={12} xxl={12} style={{ textAlign: 'center', paddingTop: '10px' }}>
                                                <Typography.Title level={3} style={{ color: 'goldenrod' }}>
                                                    {subTitulo}
                                                </Typography.Title>
                                            </Col>
                                        </Row>
                                        <Row justify='center'>
                                            <Col xs={22} sm={22} md={20} lg={18} xl={12} xxl={12}>
                                                {/* <Image alt={subTitulo} className='bdRoundBorder' src={`/assets/images/planAccion/${codigo}/${imagen}`} preview={false} onClick={() => open(url)} /> */}
                                                <div style={{ height: '450px' }}>
                                                    {
                                                        video
                                                    }
                                                </div>
                                            </Col>
                                        </Row>
                                    </div>
                                )
                            })
                        }
                        <Row>
                            <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                                <div className='bdNav'>
                                    <ul>
                                        {
                                            botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                                {titulo}
                                                <span></span><span></span><span></span><span></span>
                                            </li>)
                                        }
                                    </ul>
                                </div>
                                {/* <Space>
                                {
                                    botones.map(b =>
                                        <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                    )
                                }
                            </Space> */}
                            </Col>
                        </Row>
                    </>
                )
            }
        }
    }

    return (
        <>
            <Loader />
        </>
    )
}

export default Planificacion