// "proxy": "http://localhost:3001",

const baseURL = '/'
const token = `${baseURL}users/login`
const register = `${baseURL}users/register`
const users = `${baseURL}users/`
const supervisores = `${baseURL}supervisores/`
const paises = `${baseURL}paises/`
const perfiles = `${baseURL}perfiles/`
export const storage = {
    ADATA: 'bdAuthentication',
    DATOSGENERALES: 'bdDatosGenerales',
}
export const urls = {
    AUTENTICACION: `${token}`,
    REGISTER: `${register}`,
    GETS_PAISES: `${paises}`,
    GETS_PERFILES: `${perfiles}getPerfiles`,
    GETS_USERS: `${users}`,
    GET_USER: `${users}`,
    PUT_USER: `${users}`,
    SEND_RESET_PASSWORD: `${users}sendResetPassword`,
    UPDATE_REQUEST: `${users}updateRequest`,
    GET_RESET_PASSWORD: `${users}getResetPassword`,
    RESET_PASSWORD: `${users}resetPassword`,
    GETS_SUPERVISORES: `${supervisores}`,
    GET_AUTHENTICATION: `${users}isAuth`
}