import React, { useState, useEffect } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import { Layout, Row, Col, Image } from 'antd'
import { toast } from "react-hot-toast";
import Loader from './../components/Loader'

import { getUserName } from "../utilities";

import PresidenteService from './../service/PresidenteService'
import UsersService from "../service/UsersService";
const presidenteService = new PresidenteService()
const usersService = new UsersService();

const Presidentes = () => {
    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const { numStars, stars } = useParams()
    const botones = presidenteService.getBotones(numStars)
    const presidentes = presidenteService.gets(numStars)
    const go = (url) => navigate.push(url)

    const mostrar = (p) => {
        navigate.push(`/presidente/${p.codigo}`)
    }

    const renderPresidente = (p) => {
        const { id, nombre, imagen } = p
        return <Col key={id} xs={20} sm={10} md={10} lg={10} xl={7} xxl={5}>
            <Image alt={nombre} className='bdRoundBorder bdPuntero' src={imagen} preview={false} onClick={() => mostrar(p)} />
        </Col>
    }

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        return (
            <Layout.Content>
                <Row justify='center'>
                    <Col xs={20} sm={18} md={14} lg={12} xl={10} xxl={8}>
                        <Image alt="titulo" src={`/assets/images/presidentes/${stars}.png`} preview={false} className="bdRoundBorder" />
                    </Col>
                </Row>
                <Row justify='center' gutter={[15, 15]}>
                    {
                        presidentes.filter(p => p.visible).map(p => renderPresidente(p))
                    }
                </Row>
                <Row justify='center'>
                    <Col>
                        {
                            botones && botones.items.length ?
                                <div className='bdNav'>
                                    <ul>
                                        {
                                            botones.items.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                                {titulo}
                                                <span></span><span></span><span></span><span></span>
                                            </li>)
                                        }
                                    </ul>
                                </div>
                                : <></>
                        }
                    </Col>
                </Row>
            </Layout.Content>
        )
    }

    return (
        <Layout.Content>
            <Loader />
        </Layout.Content >
    )
}

export default Presidentes