import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { Image, Col, Layout, Row } from "antd";
import { toast } from "react-hot-toast";

import InicioService from "./../service/InicioService";
import UsersService from "../service/UsersService";
import { getUserName } from "../utilities";
import Loader from "./Loader";
const inicioService = new InicioService();
const usersService = new UsersService();

const Dashboard = () => {
  const navigate = useHistory();
  const [cargar, setCargar] = useState(true);
  const [cargando, setCargando] = useState(false);
  const [visualizar, setVisualizar] = useState(false);
  const { items } = inicioService.get();

  const go = (url) => {
    navigate.push(`/${url}`);
  };

  const renderItem = ({ id, codigo, url, imagen, props }) => {
    return (
      <Col key={id} {...props}>
        <Image
          alt={codigo}
          src={imagen}
          className="bdRoundBorder bdPuntero bdShadow"
          onClick={() => go(url)}
          preview={false}
        />
      </Col>
    );
  };

  const validar = async () => {
    setCargando(true);
    try {
      const { data } = await usersService.isAuthenticated({
        navigate,
        responsable: getUserName(),
      });
      const { estado, detalle } = data;
      if (estado === false) {
        setTimeout(() => {
          toast(detalle);
          navigate.push("/login");
        }, 2000);
      } else {
        setVisualizar(true);
      }
    } catch ({ response }) {
      const { data } = response;
      const { estado, detalle } = data;
      if (estado === false) {
        setTimeout(() => {
          toast.error(detalle);
          navigate.push("/login");
        }, 2000);
      } else {
        setVisualizar(true);
      }
    } finally {
      setCargar(false);
      setCargando(false);
    }
  };

  useEffect(() => {
    if (cargar && !cargando) validar();
  });

  // if (cargar === true && cargando === true) {
  //   return (
  //     <Layout.Content>
  //       <div
  //         style={{
  //           color: "#333333",
  //           padding: "20px 0",
  //         }}
  //       >
  //         <Image
  //           alt="BYDZYNE"
  //           src="/assets/images/logoBlack.png"
  //           style={{ height: "80px", width: "80px" }}
  //         />
  //         <span
  //           style={{
  //             marginLeft: "20px",
  //             fontSize: "1.5em",
  //             letterSpacing: "3px",
  //             fontWeight: "bold",
  //           }}
  //         >
  //           BYDZYNE
  //         </span>
  //       </div>
  //       <Loader />
  //     </Layout.Content>
  //   );
  // }

  if (cargar === false && cargando === false && visualizar === true) {
    return (
      <Layout.Content>
        <div
          style={{
            color: "#333333",
            padding: "20px 0",
          }}
        >
          <Image
            alt="BYDZYNE"
            src="/assets/images/logoBlack.png"
            style={{ height: "80px", width: "80px" }}
          />
          <span
            style={{
              marginLeft: "20px",
              fontSize: "1.5em",
              letterSpacing: "3px",
              fontWeight: "bold",
            }}
          >
            BYDZYNE
          </span>
        </div>
        <Row justify="center" gutter={[15, 15]}>
          {items.map((hi) => renderItem(hi))}
        </Row>
      </Layout.Content>
    );
  }

  return (
    <Layout.Content>
      <div
        style={{
          color: "#333333",
          padding: "20px 0",
        }}
      >
        <Image
          alt="BYDZYNE"
          src="/assets/images/logoBlack.png"
          style={{ height: "80px", width: "80px" }}
        />
        <span
          style={{
            marginLeft: "20px",
            fontSize: "1.5em",
            letterSpacing: "3px",
            fontWeight: "bold",
          }}
        >
          BYDZYNE
        </span>
      </div>
      <Loader />
    </Layout.Content>
  );

  // return (
  //   <Layout.Content>
  //     <div
  //       style={{
  //         color: "#333333",
  //         padding: "20px 0",
  //       }}
  //     >
  //       <Image
  //         alt="BYDZYNE"
  //         src="/assets/images/logoBlack.png"
  //         style={{ height: "80px", width: "80px" }}
  //       />
  //       <span
  //         style={{
  //           marginLeft: "20px",
  //           fontSize: "1.5em",
  //           letterSpacing: "3px",
  //           fontWeight: "bold",
  //         }}
  //       >
  //         BYDZYNE
  //       </span>
  //     </div>
  //     {cargar === true && cargando === true ? (
  //       <Loader />
  //     ) : (
  //       cargar === false &&
  //       cargando === false &&
  //       visualizar === true && (
  //         <Row justify="center" gutter={[15, 15]}>
  //           {items.map((hi) => renderItem(hi))}
  //         </Row>
  //       )
  //     )}
  //   </Layout.Content>
  // );
};

const comparisonFn = function (prevProps, nextProps) {
  return (
    prevProps.location.pathname === nextProps.location.pathname &&
    prevProps.colorMode === nextProps.colorMode
  );
};

export default React.memo(Dashboard, comparisonFn);
