import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'

import { Form, Row, Col, Input, Select, Button, Typography, Switch, Space, Image, Card, Alert } from 'antd'
import * as anticons from '@ant-design/icons'
import toast from 'react-hot-toast'
import PasswordChecklist from 'react-password-checklist'
import Loader from './../components/Loader'

import { getUserName, getPaises, getPerfiles, getSupervisores, getIsAdm, getIsSup, getUser } from '../utilities'
import UsersService from '../service/UsersService'
const usersService = new UsersService()

const UserNew = () => {

    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const [guardando, setGuardando] = useState(false);
    const [message, setMessage] = useState(null);
    const [user, setUser] = useState({
        idPerfil: '3'
        , idResponsable: getIsSup() && getUser()
        , activo: true
    })

    const go = () => {
        navigate.push('/users')
    }

    const onPasswordChange = (password) => setUser({ ...user, ...{ password } })

    const guardar = async (datos) => {
        const user = { ...datos, ...{ activo: datos.activo === true ? '1' : '0', responsable: getUserName() } }
        setGuardando(true)
        setMessage(null)
        try {
            const { data } = await usersService.set({ navigate, user })
            const { detalle, estado } = data
            if (estado) {
                toast.success(detalle)
                go()
            }
            else {
                toast.error(detalle)
            }
        } catch ({ response }) {
            const { data } = response
            // toast.error(data.detalle);
            setMessage(data.retorno.indexOf('IDX_CorreoUnico') > 0 ? `El correo ${user.correo} ya se encuentra registrado` : data.retorno.indexOf('IDX_PaisIdentificacionUnico') > 0 ? `La identificacin ${user.identificacion} ya se encuentra registrada` : data.detalle)
        } finally {
            setGuardando(false)
        }
    }

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        return (
            <>
                <div
                    style={{
                        color: "#333333",
                        padding: "20px 0",
                    }}
                >
                    <Image
                        alt="BYDZYNE"
                        src="/assets/images/logoBlack.png"
                        style={{ height: "80px", width: "80px" }}
                    />
                    <span
                        style={{
                            marginLeft: "20px",
                            fontSize: "1.5em",
                            letterSpacing: "3px",
                            fontWeight: "bold",
                        }}
                    >
                        BYDZYNE
                    </span>
                </div>
                <Row justify='center'>
                    <Col xs={24} sm={20} md={18} lg={16} xl={12} xxl={10} style={{ textAlign: 'center' }}>
                        <Typography.Title level={2}>
                            Registrar Usuario
                        </Typography.Title>
                    </Col>
                </Row>
                <Row justify='center'>
                    <Col xs={24} sm={20} md={18} lg={16} xl={12} xxl={10}>
                        <Card
                            bordered
                            style={{
                                backgroundColor: 'rgba(255,255,255, 0.9)',
                                padding: '10px 10px'
                            }}
                        >
                            <Form
                                layout={'vertical'}
                                initialValues={user}
                                onFinish={guardar}
                                disabled={guardando}
                            >
                                <Row justify='end' style={{ paddingBottom: '15px' }}>
                                    <Col>
                                        <Button type='link' onClick={() => go()} disabled={guardando}
                                            icon={<anticons.ArrowLeftOutlined />}
                                        >Regresar</Button>
                                    </Col>
                                </Row>
                                <Row gutter={10}>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="identificacion"
                                            label={
                                                <Typography.Text>
                                                    Identificacin
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Ingrese la identificacin',
                                                },
                                            ]}
                                        >
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="nombres"
                                            label={
                                                <Typography.Text>
                                                    Nombres
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Ingrese los nombres',
                                                },
                                            ]}>
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                </Row>
                                <Row gutter={10}>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="apellidoPaterno"
                                            label={
                                                <Typography.Text>
                                                    Apellido Paterno
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Ingrese el apellido paterno',
                                                },
                                            ]}>
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="apellidoMaterno"
                                            label={
                                                <Typography.Text>
                                                    Apellido Materno
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Ingrese el apellido materno',
                                                },
                                            ]}>
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                </Row>
                                <Row gutter={10}>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="idPais"
                                            label={
                                                <Typography.Text>
                                                    Pais
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Seleccione el pais',
                                                },
                                            ]}>
                                            <Select
                                                value={user.idPais}
                                                showSearch
                                                options={getPaises()}
                                                fieldNames={{
                                                    label: 'nombre',
                                                    value: 'id'
                                                }}
                                                optionFilterProp="nombre"
                                            />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="telefono"
                                            label={
                                                <Typography.Text>
                                                    Telfono
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Ingrese el telfono',
                                                },
                                            ]}>
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                </Row>
                                <Row gutter={10}>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="correo"
                                            label={
                                                <Typography.Text>
                                                    Correo
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Ingrese el correo',
                                                },
                                            ]}>
                                            <Input />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="password"
                                            label={
                                                <Typography.Text>
                                                    Contrasea
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Ingrese la contrasea',
                                                },
                                            ]}>
                                            <Input.Password onChange={(e) => onPasswordChange(e.target.value)} />
                                        </Form.Item>
                                        {
                                            user.password &&
                                            <PasswordChecklist
                                                rules={["minLength", "specialChar", "number", "capital"]}
                                                minLength={8}
                                                value={user.password}
                                                messages={{
                                                    minLength: "La contrasea tiene ms de 8 caracteres.",
                                                    specialChar: "La contrasea tiene caracteres especiales.",
                                                    number: "La contrasea tiene un nmero.",
                                                    capital: "La contrasea tiene una letra mayscula."
                                                }}
                                            />
                                        }
                                    </Col>
                                </Row>
                                <Row gutter={10}>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="idPerfil"
                                            label={
                                                <Typography.Text>
                                                    Perfil
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Seleccione el perfil',
                                                },
                                            ]}>
                                            <Select
                                                value={user.idPerfil}
                                                showSearch
                                                options={getPerfiles()}
                                                fieldNames={{
                                                    label: 'nombres',
                                                    value: 'id'
                                                }}
                                                optionFilterProp="nombres"
                                                disabled={!getIsAdm()}
                                            />
                                        </Form.Item>
                                    </Col>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="idResponsable"
                                            label={
                                                <Typography.Text>
                                                    Presidente
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Seleccione el presidente responsable',
                                                },
                                            ]}>
                                            <Select
                                                showSearch
                                                options={getIsAdm() === true ? getSupervisores() : getIsSup() === true ? getSupervisores().filter(s => s.id === getUser()) : []}
                                                fieldNames={{
                                                    label: 'nombres',
                                                    value: 'id'
                                                }}
                                                optionFilterProp="nombres"
                                                disabled={!getIsAdm()}
                                            />
                                        </Form.Item>
                                    </Col>
                                </Row>
                                <Row gutter={10}>
                                    <Col xs={24} sm={12} md={12} lg={12} xl={12}>
                                        <Form.Item
                                            name="activo"
                                            valuePropName="checked"
                                            label={
                                                <Typography.Text>
                                                    Activo
                                                </Typography.Text>
                                            }
                                            rules={[
                                                {
                                                    required: true,
                                                    message: 'Especifique el estado',
                                                },
                                            ]}
                                        >
                                            <Switch defaultChecked checkedChildren="S" unCheckedChildren="No"
                                            />
                                        </Form.Item>
                                    </Col>
                                </Row>
                                <Form.Item>
                                    <Row justify='center'>
                                        <Col>
                                            {
                                                message && <Alert message={message} type='warning' />
                                            }
                                        </Col>
                                    </Row>
                                    <Row justify='end' style={{ paddingTop: '15px' }}>
                                        <Col>
                                            <Space>
                                                <Button type="primary" htmlType='submit' disabled={guardando} loading={guardando}
                                                    icon={<anticons.SaveFilled />}
                                                >
                                                    {guardando ? 'Guardando...' : 'Guardar'}
                                                </Button>
                                            </Space>
                                        </Col>
                                    </Row>
                                    <Row justify='end' style={{ paddingTop: '15px' }}>
                                        <Col>
                                            <Button type='link' onClick={() => go()} disabled={guardando}
                                                icon={<anticons.ArrowLeftOutlined />}
                                            >Regresar</Button>
                                        </Col>
                                    </Row>
                                </Form.Item>
                            </Form>
                        </Card>
                    </Col>
                </Row>
            </>
        )
    }

    return (
        <>
            <Loader />
        </>
    )
}

export default UserNew