import React from 'react'
import { Image, Row, Col } from 'antd'
import { useHistory } from 'react-router-dom'

const Home = () => {
    const history = useHistory()
    return (
        <div style={{ width: '100%', height: '100vh', backgroundColor: '#000000' }}>
            <div style={{
                background: 'linear-gradient(to right, rgba(163,43,213,0.35), rgba(20,84,219,0.45))',
                width: '100%',
                height: '100vh',
                position: 'fixed'
            }}>
                <div style={{
                    color: '#ffffff',
                    borderBottom: 'solid 1px #666666',
                    padding: '20px 30px',
                }}>
                    <Image alt='BYDZYNE' src='/assets/images/logo.png' style={{ height: '60px', width: '60px' }} />
                    <span style={{
                        marginLeft: '20px',
                        fontSize: '1.5em',
                        letterSpacing: '3px',
                        fontWeight: 'bold'
                    }}>
                        BYDZYNE
                    </span>
                </div>
            </div>
            <video id="my-video" autoPlay loop style={{ width: '100%', height: '98%', zIndex: '0' }}>
                <source src="/assets/bienvenida.mp4" type="video/mp4" />
            </video>
            <div style={{
                position: 'absolute',
                width: '100%',
                height: '100vh',
                top: 0,
                left: 0,
                zIndex: '1001',
                color: '#ffffff',
            }}>
                <Row justify='center' style={{ height: '100vh' }}>
                    <Col style={{ textAlign: 'center' }} xs={22} sm={20} md={14} lg={10} xl={8} xxl={6}>
                        <div style={{ marginTop: "60%", }}>
                            <span style={{
                                fontSize: '4em',
                                letterSpacing: '3.5px',
                                fontWeight: 'bold',
                                display: 'block'
                            }}>
                                <Image alt='Bienvenido' src='/assets/images/bienvenidoTitulo.png' preview={false} />
                            </span>
                            <span style={{
                                fontSize: '2em',
                                letterSpacing: '3px',
                                fontWeight: 'bold',
                                display: 'block',
                            }}>
                                <Image alt='Bienvenido' src='/assets/images/bienvenidoEquipo.png' preview={false} />
                            </span>
                            <div className='bdNav' style={{ zIndex: '1000', textAlign: 'center' }}>
                                <ul>
                                    <li style={{ width: '200px' }} className='dbPuntero' onClick={() => { history.push('login') }}>
                                        Empezar
                                        <span></span><span></span><span></span><span></span>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </Col>
                </Row>
            </div>
        </div >
    )
}

export default Home