import React, { useState, useEffect } from 'react'
import { useParams, useHistory } from 'react-router-dom'
import { Col, Row, Image, Typography } from 'antd'
import { toast } from "react-hot-toast";
import Loader from './../components/Loader'

import { getUserName } from "../utilities";

import UsersService from "../service/UsersService";
import ServiciosService from './../service/ServiciosService'
const usersService = new UsersService();
const serviciosService = new ServiciosService()

const Servicio = () => {
    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const { servicio } = useParams()
    const detalleServicio = serviciosService.get(servicio)

    const go = (url) => {
        navigate.push(`/${url}`);
    }

    const renderServicio = ({ id, nombre, imagen, url, video }) => <Col key={id} xs={24} sm={22} md={16} lg={16} xl={12} xxl={12} style={{ textAlign: 'center' }}>
        <Typography.Title level={5} style={{ color: '#ffffff', letterSpacing: '1px' }}>
            {nombre}
        </Typography.Title>
        {
            video ?
                <div style={{ height: '330px' }}>
                    {
                        video
                    }
                </div>
                : <Image alt={nombre} className='bdRoundBorder bdPuntero' src={`/assets/images/planAccion/servicios/${imagen}`} onClick={() => go(url)} preview={false} />
        }
    </Col>

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        if (detalleServicio && detalleServicio.id) {
            const { titulo, subTitulo, botones, items } = detalleServicio
            return (
                <>
                    <Row justify='center' gutter={[20, 20]}>
                        <Col xs={24} sm={24} md={24} lg={22} xl={22} xxl={16}>
                            <Row justify='center'>
                                <Col span={24} style={{ textAlign: 'center' }}>
                                    <Typography.Title level={1} style={{ color: 'goldenrod', textTransform: 'uppercase' }}>
                                        {titulo}
                                    </Typography.Title>
                                    <Typography.Title level={2}>
                                        {subTitulo}
                                    </Typography.Title>
                                </Col>
                                {
                                    items && items.length > 0 &&
                                    <Col span={24}>
                                        <Row gutter={[15, 15]} justify='center'>
                                            {
                                                items.filter(s => s.visible === true).map(s => renderServicio(s))
                                            }
                                        </Row>
                                    </Col>
                                }
                            </Row>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                            <div className='bdNav'>
                                <ul>
                                    {
                                        botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                            {titulo}
                                            <span></span><span></span><span></span><span></span>
                                        </li>)
                                    }
                                </ul>
                            </div>
                            {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                        </Col>
                    </Row>
                </>
            )
        }
    }

    return (
        <>
            <Loader />
        </>
    )
}

export default Servicio