import React from 'react'
import { Popover, FloatButton, List, Button, Typography } from 'antd'
import * as anticons from '@ant-design/icons'
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min'
import { cerrarSesion, getAutenticado, getName, getOptions, getUserName } from './utilities'

const AppUserInfo = () => {
    const navigate = useHistory()
    const go = (key) => {
        if (key === 'inicio') {
            navigate.push('/inicio')
        } else if (key === 'gestionUsuarios') {
            navigate.push('/users')
        } else if (key === 'cerrarSesion') {
            cerrarSesion(navigate);
        }
    }
    const renderOption = ({ id, nombre, titulo }) => <Button key={id} type='link' onClick={() => go(nombre)}>{titulo}</Button>
    const data = getOptions().map(o => renderOption(o))
    const text = <>
        <Typography.Text style={{ textTransform: 'uppercase' }}>
            {
                getName()
            }
        </Typography.Text>
        <br />
        <Typography.Text type='secondary' style={{ fontWeight: 'normal' }}>
            {
                getUserName()
            }
        </Typography.Text>
    </>
    const content = (
        <List
            bordered={false}
            size='small'
            dataSource={data}
            renderItem={(item) => (
                <List.Item style={{
                    padding: '2px 0px',
                    textAlign: 'left'
                }}>
                    {item}
                </List.Item>
            )}
        />
    );
    if (getAutenticado() && getAutenticado() === true) {
        return (
            <>
                <div style={{
                    position: 'absolute',
                    border: 'solid 1p #ff0000',
                    top: 10,
                    right: 10,
                }}>
                    <Popover placement="bottomRight" title={text} content={content} trigger='hover'>
                        <FloatButton style={{
                            position: 'absolute',
                            top: 10,
                            right: 10
                        }}
                            icon={<anticons.UserOutlined />}
                            shape='square'
                        />
                    </Popover>
                </div>
            </>
        )
    } else {
        return <></>
    }
}

export default AppUserInfo