import React, { useState } from 'react'
import { useHistory, Link } from 'react-router-dom'
import { Image, Card, Form, Input, Button, Typography, Row, Col, Alert } from 'antd'

// import toast from 'react-hot-toast'

import { eliminarSesion, setDatosAutenticacion, setDatosGenerales } from '../utilities'

import AuthenticationService from './../service/AuthenticationService'
const authenticationService = new AuthenticationService()

const Ingresar = () => {
    const history = useHistory()
    const [isLoading, setIsLoading] = useState(false);
    const [message, setMessage] = useState(null);
    const usuario = { login: "", password: "" };
    const ingresar = (datosUsuario) => {
        const { login, password } = datosUsuario
        if (login === "") {
            alert("Ingrese su usuario");
            return;
        }
        if (password === "") {
            alert("Ingrese su contrasea");
            return;
        }
        setMessage(null);
        setIsLoading(true);
        eliminarSesion();
        authenticationService
            .login(datosUsuario)
            .then(({ data }) => {
                setDatosAutenticacion({
                    token: data.retorno.retorno,
                    user: {
                        authenticated: true,
                        role: data.usuario[0].idPerfil,
                        code: data.usuario[0].id,
                        login: data.usuario[0].correo,
                        name: `${data.usuario[0].nombres} ${data.usuario[0].apellidoPaterno} ${data.usuario[0].apellidoMaterno}`,
                    },
                    options: data.options
                });
                setDatosGenerales({
                    perfiles: data.perfiles,
                    paises: data.paises,
                    supervisores: data.supervisores
                });
                const { retorno } = data;
                if (retorno.estado) {
                    history.push("inicio")
                }
            })
            .catch(({ response }) => {
                const { data } = response
                // toast.error(data.detalle);
                setMessage(data.detalle);
            })
            .finally(() => {
                setIsLoading(false);
            })

    };
    return (
        <div style={{ width: '100%', height: '100vh', backgroundColor: '#000000' }}>
            <div style={{
                background: 'linear-gradient(to right, rgba(163,43,213,0.35), rgba(20,84,219,0.45))',
                width: '100%',
                height: '100vh',
                position: 'fixed'
            }}>
                <div style={{
                    color: '#ffffff',
                    borderBottom: 'solid 1px #666666',
                    padding: '20px 30px',
                }}>
                    <Image alt='BYDZYNE' src='/assets/images/logo.png' style={{ height: '60px', width: '60px' }} />
                    <span style={{
                        marginLeft: '20px',
                        fontSize: '1.5em',
                        letterSpacing: '3px',
                        fontWeight: 'bold'
                    }}>
                        BYDZYNE
                    </span>
                </div>
            </div>
            <video id="my-video" autoPlay loop style={{ width: '100%', height: '98%', zIndex: '0' }}>
                <source src="/assets/bienvenida.mp4" type="video/mp4" />
            </video>
            <div style={{
                position: 'fixed',
                width: '100%',
                height: '100vh',
                top: 0,
                left: 0,
                zIndex: '1001',
                color: '#ffffff',
            }}>
                <Row justify='center'>
                    <Col style={{ textAlign: 'center' }} xs={20} sm={18} md={12} lg={8} xl={6} xxl={6}>
                        <Card
                            bordered={false}
                            title={<Typography.Title level={4} style={{ color: '#333333', letterSpacing: '0.2em', paddingBottom: '10px 0' }}>
                                INICIAR SESIN
                            </Typography.Title>}
                            style={{
                                background: "rgba(255, 255, 255, 0.85)",
                                padding: "10px 25px",
                                marginTop: "50%"
                            }}
                        >
                            <Form
                                name="normal_login"
                                layout="vertical"
                                className="login-form"
                                initialValues={usuario}
                                onFinish={ingresar}
                            >
                                <Form.Item
                                    name="login"
                                    label="Usuario"
                                    rules={[
                                        {
                                            required: true,
                                            message: "Ingrese su usuario",
                                        },
                                    ]}
                                >
                                    <Input />
                                </Form.Item>
                                <Form.Item
                                    name="password"
                                    label="Contrasea"
                                    rules={[
                                        {
                                            required: true,
                                            message: "Ingrese su contrasea",
                                        },
                                    ]}
                                >
                                    <Input type="password" />
                                </Form.Item>

                                {
                                    message &&
                                    <Form.Item>
                                        <Alert message={message} type="warning" showIcon />
                                    </Form.Item>
                                }


                                <Form.Item>
                                    <Button
                                        type="primary"
                                        htmlType="submit"
                                        className="login-form-button"
                                        style={{ width: "100%" }}
                                        loading={isLoading}
                                        disabled={isLoading}
                                    >
                                        Ingresar
                                    </Button>
                                </Form.Item>

                                <Row>
                                    <Col span={12} style={{ textAlign: 'left' }}>
                                        <Form.Item>
                                            <Link to="/register">Registrarme</Link>
                                        </Form.Item>
                                    </Col>
                                    <Col span={12} style={{ textAlign: 'right' }}>
                                        <Form.Item>
                                            <Link to="/forgotPassword">Olvide mi contrasea</Link>
                                        </Form.Item>
                                    </Col>
                                </Row>

                            </Form>
                        </Card>
                    </Col>
                </Row>
            </div>
        </div>
    )
}

export default Ingresar