import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Col, Row, Image, Typography } from 'antd'
import { toast } from "react-hot-toast";
import Loader from './../components/Loader'

import { getUserName } from "../utilities";

import UsersService from "../service/UsersService";
import TestimonioService from './../service/TestimonioService'
const usersService = new UsersService();
const testimonioService = new TestimonioService()

const Testimonios = () => {
    const navigate = useHistory()
    const [cargar, setCargar] = useState(true);
    const [cargando, setCargando] = useState(false);
    const [visualizar, setVisualizar] = useState(false);
    const testimonios = testimonioService.gets()

    const go = (url) => {
        navigate.push(`/${url}`);
    }

    const renderTestimonio = ({ id, titulo, nombre, imagen, url }) => <Col key={id} xs={24} sm={18} md={12} lg={10} xl={8} xxl={6} style={{ textAlign: 'center' }}>
        <Typography.Title level={4} style={{ color: 'goldenrod', letterSpacing: '1px', textTransform: 'uppercase' }}>
            {titulo}
        </Typography.Title>
        <Image alt={nombre} className='bdRoundBorder bdPuntero' src={`/assets/images/planAccion/testimonios/${imagen}`} onClick={() => go(url)} preview={false} />
    </Col>

    const validar = async () => {
        setCargando(true);
        try {
            const { data } = await usersService.isAuthenticated({
                navigate,
                responsable: getUserName(),
            });
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } catch ({ response }) {
            const { data } = response;
            const { estado, detalle } = data;
            if (estado === false) {
                setTimeout(() => {
                    toast.error(detalle);
                    navigate.push("/login");
                }, 2000);
            } else {
                setVisualizar(true);
            }
        } finally {
            setCargar(false);
            setCargando(false);
        }
    };

    useEffect(() => {
        if (cargar && !cargando) validar();
    });

    if (cargar === false && cargando === false && visualizar === true) {
        if (testimonios && testimonios.id) {
            const { codigo, titulo, imagenTitulo, subTitulo, botones, items } = testimonios
            return (
                <>
                    <Row justify='center' gutter={[20, 20]}>
                        <Col xs={22} sm={20} md={14} lg={12} xl={10} xxl={8}>
                            <Row>
                                <Col span={24}>
                                    {
                                        imagenTitulo ?
                                            <Image alt={titulo} src={`/assets/images/planAccion/${codigo}/${imagenTitulo}`} preview={false} />
                                            :
                                            <>
                                                <Typography.Title level={1}>
                                                    {titulo}
                                                </Typography.Title>
                                                <Typography.Title level={2}>
                                                    {subTitulo}
                                                </Typography.Title>
                                            </>
                                    }
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row justify='center' gutter={[20, 20]}>
                        <Col xs={24} sm={24} md={22} lg={22} xl={20} xxl={18}>
                            <Row justify='center'>
                                <Col span={24}>
                                    <Typography.Title level={2} style={{ color: 'goldenrod', textAlign: 'center', textTransform: 'uppercase' }}>
                                        {subTitulo}
                                    </Typography.Title>
                                </Col>
                                {
                                    items && items.length > 0 &&
                                    <Col span={24}>
                                        <Row gutter={[15, 15]} justify='center'>
                                            {
                                                items.filter(t => t.visible === true).map(s => renderTestimonio(s))
                                            }
                                        </Row>
                                    </Col>
                                }
                            </Row>
                        </Col>
                    </Row>
                    <Row>
                        <Col xs={24} sm={24} md={24} lg={24} xl={24} xxl={24} style={{ textAlign: 'center', paddingTop: '20px' }}>
                            <div className='bdNav'>
                                <ul>
                                    {
                                        botones.map(({ id, titulo, url }) => <li key={id} onClick={() => go(url)}>
                                            {titulo}
                                            <span></span><span></span><span></span><span></span>
                                        </li>)
                                    }
                                </ul>
                            </div>
                            {/* <Space>
                            {
                                botones.map(b =>
                                    <Button key={b.id} type={b.type} icon={b.icon} onClick={() => go(b.url)}>{b.titulo}</Button>
                                )
                            }
                        </Space> */}
                        </Col>
                    </Row >
                </>
            )
        }
    }

    return (
        <>
            <Loader />
        </>
    )
}

export default Testimonios